/*** JVP interface, Apex Hu 2021.07.16 */
'use strict';

import Event from './events';
import FlowController from './controller/flow-controller';
import BufferController from './controller/buffer-controller';
import EventEmitter from 'events';
import AudioRecorder from './loader/audio-recorder';
import WebsocketLoader from './loader/websocket-loader';

class Jvp {

  static get version() {
    // replaced with browserify-versionify transform
    return '0.1.0' + 'v.0.0.0.1';
  }

  static isSupported() {
    return (window.MediaSource && typeof window.MediaSource.isTypeSupported === 'function' && window.MediaSource.isTypeSupported('video/mp4; codecs="avc1.42c01f,mp4a.40.2"'));
  }

  static get Events() {
    return Event;
  }

  static get DefaultConfig() {
    if (!Jvp.defaultConfig) {
      Jvp.defaultConfig = {
        // autoStartLoad: true,
        // startPosition: -1,
        debug: false,
        // fLoader: undefined,
        // loader: XhrLoader,
        //loader: FetchLoader,
        // fmp4FileUrl: 'xxxx.mp4',
        // fragLoadingTimeOut: 20000,
        // fragLoadingMaxRetry: 6,
        // fragLoadingRetryDelay: 1000,
        // fragLoadingMaxRetryTimeout: 64000,
        // fragLoadingLoopThreshold: 3,
        // forceKeyFrameOnDiscontinuity: true,
        // appendErrorMaxRetry: 3,
        jtt1078Version: '2011',   // 2011、2019
        onWaiting: undefined,     // callback func
        onPlaying: undefined,     // callback func
        onEvent: undefined,       // callback func
        onError: undefined,       // callback func
        onTimeUpdate: undefined   // callback func
      };
    }
    return Jvp.defaultConfig;
  }

  static set DefaultConfig(defaultConfig) {
    Jvp.defaultConfig = defaultConfig;
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  constructor(config = {}) {

    var defaultConfig = Jvp.DefaultConfig;
    for (var prop in defaultConfig) {
      if (prop in config) { continue; }
      config[prop] = defaultConfig[prop];
    }
    this.config = config;

    // observer setup
    var observer = this.observer = new EventEmitter();
    observer.trigger = function trigger(event, ...data) {
      observer.emit(event, event, ...data);
    };
    observer.off = function off(event, ...data) {
      observer.removeListener(event, ...data);
    };
    this.on = observer.on.bind(observer);
    this.off = observer.off.bind(observer);
    this.trigger = observer.trigger.bind(observer);

    this.flowController = new FlowController(this);
    this.bufferController = new BufferController(this);
    this.websocketLoader = new WebsocketLoader(this);
    this.mediaType = undefined;
    this.audioRecorder = undefined;

    // 浏览器类型
    this.browserType = 0;
    if (navigator.userAgent.toLowerCase().indexOf("msie") !== -1) {
      this.browserType = 0;
    }
    if (navigator.userAgent.toLowerCase().indexOf("firefox") !== -1) {
      this.browserType = 1;
    }
    if (navigator.userAgent.toLowerCase().indexOf("chrome") !== -1) {
      this.browserType = 2;
    }
    if (navigator.userAgent.toLowerCase().indexOf("edge") !== -1) {
      this.browserType = 3;
    }

    // 注册本地事件    
    this.on(Event.UI_WAITING, this.onWaiting.bind(this));
    this.on(Event.UI_PLAYING, this.onPlaying.bind(this));
    this.on(Event.UI_EVENT, this.onEvent.bind(this));
    this.on(Event.UI_TIME_UPDATE, this.onTimeUpdate.bind(this));
    this.on(Event.ERROR, this.onError.bind(this));
  }

  destroy() {
    this.flowController.destroy();
    this.bufferController.destroy();
    this.websocketLoader.destroy();
    if (this.audioRecorder) this.audioRecorder.destroy();
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  onWaiting() {
    if (this.config.onWaiting) this.config.onWaiting(this);
  }

  onPlaying() {
    if (this.config.onPlaying) this.config.onPlaying(this);
  }

  onEvent(eventName) {
    if (this.config.onEvent) this.config.onEvent(this, eventName);
  }

  // currentTime: seconds
  onTimeUpdate(eventName, data) {
    if (this.config.onTimeUpdate) this.config.onTimeUpdate(this, eventName, data.currentTime, data.receivedBytesPerSecond, data.uploadBytesPerSecond);
  }

  onError(data) {
    if (this.config.onError) this.config.onError(this, data);
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  attachMedia(media, channelName = 'chX', mediaType = 'H264Raw', websocketProtocol = 'http', websocketIP, websocketPort, websocketName, wsParams) { // 'H264Raw' 'FMp4' 'JTT1078'
    this.trigger(Event.UI_WAITING, {});
    this.mediaType = mediaType;
    this.media = media;
    this.trigger(Event.MEDIA_ATTACHING, { media: media, channelName: channelName, mediaType: mediaType, websocketProtocol: websocketProtocol, websocketIP: websocketIP, websocketPort: websocketPort, websocketName: websocketName, wsParams: wsParams });
  }

  startCommunication() {
    if (!this.audioRecorder) {
      AudioRecorder.get(this, this.config, function (rec) {
        this.audioRecorder = rec;
        this.audioRecorder.start();
      }.bind(this));
    }
  }

  stopCommunication() {
    if (this.audioRecorder) {
      this.audioRecorder.stop();
      this.audioRecorder.destroy();
      this.audioRecorder = undefined;
    }
  }

  priintBinaryArray(array, prevText) {
    var idx = 0;
    while (true) {
      var startIdx = idx * 1000;
      var endIdx = (idx + 1) * 1000 - 1;
      if (endIdx >= array.length) endIdx = array.length - 1;
      if (startIdx >= array.length) break;

      var result = "";
      for (var i = startIdx; i <= endIdx; i++) {
        var char = array[i].toString(16).toUpperCase();
        if (char.length == 1) result += '0' + char; else result += char;
      }
      console.log(prevText + ' Array ' + idx + ': ' + result);
      idx++;
    }
  }
}

export default Jvp;
