/*** fMP4 remuxer */
import AAC from './aac-helper';
import MP4 from './mp4-generator';
import Event from '../events';
import { logger } from '../utils/logger';
import { ErrorTypes, ErrorDetails } from '../errors';
import '../utils/polyfill';

class MP4Remuxer {

  constructor(observer, id, config) {
    this.observer = observer;
    this.id = id;
    this.config = config;

    this.PES_TIMESCALE = 90000; // PTS/DTS的初始值，一般来源于录制的视频的频率，按照h264的设定是90HZ
    this.PES_TIMESCALE_SOUN = 8e3;
    this.PES2MP4SCALEFACTOR = 4;
    this.MP4_TIMESCALE = this.PES_TIMESCALE / this.PES2MP4SCALEFACTOR;
    this.H264_TIMEBASE = 3000; //3000; // 3000->320
    this.SOUN_TIMEBASE = 1024;

    this.ISGenerated = false;
    this.nextAvcDts = 320; //90300;
    this.nextAacPts = 1024;
  }

  destroy() {
  }

  get passthrough() {
    return false;
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  insertDiscontinuity() {
    this._initPTS = this._initDTS = undefined;
  }

  switchLevel() {
    this.ISGenerated = false;
  }

  // avFalg=2：仅视频
  pushVideo(level, sn, videoTrack, timeOffset, contiguous) {
    this.level = level;
    this.sn = sn;

    // generate Init Segment if needed
    if (!this.ISGenerated) {
      this.generateVideoIS(videoTrack, timeOffset);
    }

    if (this.ISGenerated) {
      if (videoTrack.samples.length) {
        this.remuxVideo_2(videoTrack, timeOffset, contiguous);
      }
    }
  }

  // avFalg=1：仅音频
  pushAudio(level, sn, auditTrack, timeOffset, contiguous) {// t, i, a, s, r
    this.level = level;
    this.sn_soun = sn;

    if (!this.ISGenerated) {
      this.generateAudioIS(auditTrack, timeOffset);
    }

    if (this.ISGenerated) {
      this.remuxAudio_2(auditTrack, timeOffset, contiguous);
    }
  }

  // avFalg=0：音视频-视频
  pushVideo2(level, sn, videoTrack, auditTrack, timeOffset, contiguous) { // t i a s r n
    this.level = level;
    this.sn = sn;

    // generate Init Segment if needed
    if (!this.ISGenerated) {
      this.generateVideoAudioIS(videoTrack, auditTrack, timeOffset); // a s r
    }
    if (this.ISGenerated) {
      if (auditTrack.samples.length) {
        this.remuxAudio_2(auditTrack, timeOffset, contiguous);
      }
      if (videoTrack.samples.length) {
        this.remuxVideo_2(videoTrack, timeOffset, contiguous, auditTrack); // a r n s
      }
    }
  }

  // avFalg=0：音视频-音频
  pushAudio2(level, sn, auditTrack, timeOffset, contiguous) {
    this.level = level;
    this.sn_soun = sn;

    if (this.ISGenerated) {
      this.remuxAudio_2(auditTrack, timeOffset, contiguous);
    }
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  generateVideoIS(videoTrack, timeOffset) {
    var observer = this.observer,
      videoSamples = videoTrack.samples,
      pesTimeScale = this.PES_TIMESCALE,
      tracks = {},
      data = { id: this.id, level: this.level, sn: this.sn, tracks: tracks, unique: false },
      computePTSDTS = (this._initPTS === undefined),
      initPTS, initDTS;

    if (computePTSDTS) {
      initPTS = initDTS = Infinity;
    }

    // video
    if (videoTrack.sps && videoTrack.pps && videoSamples.length) {
      videoTrack.timescale = this.PES_TIMESCALE; //90000;//this.MP4_TIMESCALE;
      videoTrack.duration = this.H264_TIMEBASE;
      tracks.video = { container: 'video/mp4', codec: videoTrack.codec, initSegment: MP4.initSegment([videoTrack]), metadata: { width: videoTrack.width, height: videoTrack.height } };

      if (computePTSDTS) {
        initPTS = 0; //Math.min(initPTS, videoSamples[0].pts - this.H264_TIMEBASE);
        initDTS = 0; //Math.min(initDTS, videoSamples[0].dts - this.H264_TIMEBASE);
      }
    }

    // trigger event
    if (Object.keys(tracks).length) {
      observer.trigger(Event.FRAG_PARSING_INIT_SEGMENT, data);
      this.ISGenerated = true;
      if (computePTSDTS) {
        this._initPTS = initPTS;
        this._initDTS = initDTS;
      }
    } else {
      console.log("generateVideoIS ERROR==> ", ErrorTypes.MEDIA_ERROR);
    }
  }

  generateAudioIS(audioTrack, timeOffset) {
    var observer = this.observer,
      audioSamples = audioTrack.samples,
      tracks = {},
      data = { id: 2, level: this.level, sn: this.sn_soun, tracks: tracks, unique: false },
      computePTSDTS = this._initPTS === undefined,
      initPTS,
      initDTS;

    if (computePTSDTS) {
      initPTS = initDTS = Infinity;
    }

    // audio
    if (audioTrack.config && audioSamples.length) {
      audioTrack.timescale = audioTrack.samplerate;
      audioTrack.duration = this.SOUN_TIMEBASE;

      tracks.audio = { container: "video/mp4", codec: audioTrack.codec, initSegment: MP4.initSegment([audioTrack]), ISType: "audio", metadata: { channelCount: audioTrack.channelCount } };

      if (computePTSDTS) {
        initPTS = initDTS = 0; //Math.min(initPTS, audioSamples[0].pts - this.SOUN_TIMEBASE);
      }
    }

    // trigger event
    if (Object.keys(tracks).length) {
      observer.trigger(Event.FRAG_PARSING_INIT_SEGMENT, data);
      this.ISGenerated = true;
      if (computePTSDTS) {
        this._initPTS = initPTS;
        this._initDTS = initDTS;
      }
    } else {
      console.log("generateAudioIS ERROR==> ", ErrorTypes.MEDIA_ERROR);
    }
  }

  generateVideoAudioIS(videoTrack, audioTrack, timeOffset) {
    var observer = this.observer,
      videoSamples = videoTrack.samples,
      tracks = {},
      data = { id: this.id, level: this.level, sn: this.sn, tracks: tracks, unique: false },
      computePTSDTS = (this._initPTS === undefined),
      initPTS,
      initDTS;

    if (computePTSDTS) {
      initPTS = initDTS = Infinity;
    }

    var avTracks = [];
    if (videoTrack.sps && videoTrack.pps && videoSamples.length) {
      videoTrack.timescale = this.PES_TIMESCALE;
      videoTrack.duration = this.H264_TIMEBASE;
      avTracks.push(videoTrack);

      audioTrack.timescale = this.PES_TIMESCALE_SOUN;
      audioTrack.duration = this.SOUN_TIMEBASE;
      audioTrack.channelCount = 1;
      avTracks.push(audioTrack);

      tracks.audio = {
        container: "video/mp4",
        codec: audioTrack.codec,
        initSegment: MP4.initSegment([audioTrack]),
        ISType: "videoaudio",
        metadata: { channelCount: audioTrack.channelCount }
      };

      tracks.video = {
        container: "video/mp4",
        codec: videoTrack.codec,
        initSegment: MP4.initSegment([videoTrack]),
        ISType: "videoaudio",
        metadata: { width: videoTrack.width, height: videoTrack.height }
      };

      if (computePTSDTS) {
        initPTS = 0; //Math.min(initPTS, videoSamples[0].pts - this.H264_TIMEBASE);
        initDTS = 0; //Math.min(initDTS, videoSamples[0].dts - this.H264_TIMEBASE);
      }
    }

    // trigger event
    if (Object.keys(tracks).length) {
      observer.trigger(Event.FRAG_PARSING_INIT_SEGMENT, data);
      this.ISGenerated = true;
      if (computePTSDTS) {
        this._initPTS = initPTS;
        this._initDTS = initDTS;
      }
    } else {
      console.log("generateVideoAudioIS ERROR==> ", ErrorTypes.MEDIA_ERROR);
    }
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  remuxVideo_2(track, timeOffset, contiguous, audioTrackLength) {
    var offset = 8,
      pesTimeScale = this.PES_TIMESCALE,
      pes2mp4ScaleFactor = this.PES2MP4SCALEFACTOR,
      mp4SampleDuration,
      mdat, moof,
      firstPTS, firstDTS,
      nextDTS,
      inputSamples = track.samples,
      outputSamples = [];

    /* concatenate the video data and construct the mdat in place(need 8 more bytes to fill length and mpdat type) */
    mdat = new Uint8Array(track.len + (4 * track.nbNalu) + 8);
    let view = new DataView(mdat.buffer);
    view.setUint32(0, mdat.byteLength);
    mdat.set(MP4.types.mdat, 4);

    var sampleDuration = 0;
    let ptsnorm, dtsnorm, mp4Sample, lastDTS;

    let firstAvcSampleDts;
    let totalDuration = 0;
    for (let i = 0; i < inputSamples.length; i++) {
      let avcSample = inputSamples[i], mp4SampleLength = 0, compositionTimeOffset;

      // convert NALU bitstream to MP4 format (prepend NALU with size field)
      while (avcSample.units.units.length) {
        let unit = avcSample.units.units.shift();
        view.setUint32(offset, unit.data.byteLength);
        offset += 4;
        mdat.set(unit.data, offset);
        offset += unit.data.byteLength;
        mp4SampleLength += 4 + unit.data.byteLength;
      }

      if (firstAvcSampleDts == undefined) {
        firstAvcSampleDts = avcSample.dts;
      }
      totalDuration += avcSample.duration;

      mp4SampleDuration = avcSample.duration;
      let pts = avcSample.pts - this._initPTS;
      let dts = avcSample.dts - this._initDTS;
      dts = Math.min(pts, dts);

      if (lastDTS !== undefined) {
        ptsnorm = this._PTSNormalize(pts, lastDTS);
        dtsnorm = this._PTSNormalize(dts, lastDTS);
        sampleDuration = (dtsnorm - lastDTS)
        if (sampleDuration <= 0) {
          logger.log(`invalid sample duration at PTS/DTS: ${avcSample.pts}/${avcSample.dts}|dts norm: ${dtsnorm}|lastDTS: ${lastDTS}:${sampleDuration}`);
          sampleDuration = 1;
        }
      }
      else {
        var nextAvcDts = this.nextAvcDts, delta;
        ptsnorm = this._PTSNormalize(pts, nextAvcDts);
        dtsnorm = this._PTSNormalize(dts, nextAvcDts);
        if (nextAvcDts) {
          delta = Math.round((dtsnorm - nextAvcDts));
          if (/*contiguous ||*/ Math.abs(delta) < 120) { // 600 => 120
            if (delta) {
              // if (delta > 1) {
              //   logger.log(`AVC:${delta} ms hole between fragments detected,filling it`);
              // } else if (delta < -1) {
              //   logger.log(`AVC:${(-delta)} ms overlapping between fragments detected`);
              // }
              dtsnorm = nextAvcDts;
              ptsnorm = Math.max(ptsnorm - delta, dtsnorm);
              logger.log(`Video/PTS/DTS adjusted: ${ptsnorm}/${dtsnorm},delta:${delta}`);
            }
          }
        }
        this.firstPTS = Math.max(0, ptsnorm);
        this.firstDTS = Math.max(0, dtsnorm);
        sampleDuration = 0.04; // 0.03=>0.04
      }

      outputSamples.push({
        size: mp4SampleLength,
        duration: avcSample.duration, // this.H264_TIMEBASE, // mp4SampleDuration, //
        cts: 0,
        flags: { isLeading: 0, isDependedOn: 0, hasRedundancy: 0, degradPrio: 0, dependsOn: avcSample.key ? 2 : 1, isNonSync: avcSample.key ? 0 : 1 }
      });
      lastDTS = dtsnorm;
    }

    // var lastSampleDuration = 0;
    // if (outputSamples.length >= 2) {
    //   lastSampleDuration = outputSamples[outputSamples.length - 2].duration;
    //   outputSamples[0].duration = lastSampleDuration;
    // }

    //this.nextAvcDts = dtsnorm + lastSampleDuration;
    let dropped = track.dropped;
    track.len = 0;
    track.nbNalu = 0;
    track.dropped = 0;
    if (outputSamples.length && navigator.userAgent.toLowerCase().indexOf('chrome') > -1) {
      let flags = outputSamples[0].flags;
      flags.dependsOn = 2;
      flags.isNonSync = 0;
    }

    dtsnorm = firstAvcSampleDts;
    track.samples = outputSamples;
    track.duration = totalDuration;
    moof = MP4.moof(track.sequenceNumber++, dtsnorm, track);
    track.samples = [];

    let data = {
      id: this.id,
      level: this.level,
      sn: this.sn,
      data1: moof,
      data2: mdat,
      startPTS: ptsnorm,
      endPTS: ptsnorm,
      startDTS: dtsnorm,
      endDTS: dtsnorm,
      type: 'video',
      nb: outputSamples.length,
      dropped: dropped
    };
    this.observer.trigger(Event.FRAG_PARSING_DATA, data);
    return data;
  }

  remuxAudio_2(track, timeOffset, contiguous) {
    let pesTimeScale = this.PES_TIMESCALE_SOUN,
      mp4timeScale = track.timescale,
      pes2mp4ScaleFactor = pesTimeScale / mp4timeScale,
      expectedSampleDuration = track.timescale * 1024 / track.samplerate;
    var view,
      offset = 8,
      aacSample, mp4Sample,
      unit,
      mdat, moof,
      firstPTS, firstDTS, lastDTS,
      pts, dts, ptsnorm, dtsnorm,
      samples = [],
      samples0 = [];

    track.samples.sort(function (a, b) { return (a.pts - b.pts); });
    samples0 = track.samples;

    let nextAacPts = (contiguous ? this.nextAacPts : timeOffset * pesTimeScale);

    // If the audio track is missing samples, the frames seem to get "left-shifted" within the
    // resulting mp4 segment, causing sync issues and leaving gaps at the end of the audio segment.
    // In an effort to prevent this from happening, we inject frames here where there are gaps.
    // When possible, we inject a silent frame; when that's not possible, we duplicate the last frame.
    let firstPtsNorm = this._PTSNormalize(samples0[0].pts - this._initPTS, nextAacPts), pesFrameDuration = expectedSampleDuration * pes2mp4ScaleFactor;
    var nextPtsNorm = firstPtsNorm + pesFrameDuration;
    let firstAAcSampleDts;
    let totalDuration = 0;

    while (samples0.length) {
      aacSample = samples0.shift();
      unit = aacSample.units.units.shift();

      pts = aacSample.pts - this._initDTS;
      dts = aacSample.dts - this._initDTS;
      //logger.log(`Audio/PTS:${Math.round(pts/90)}`);

      if (firstAAcSampleDts == undefined) {
        firstAAcSampleDts = aacSample.dts;
      }
      totalDuration += aacSample.duration;

      // if not first sample
      if (lastDTS !== undefined) {
        ptsnorm = this._PTSNormalize(pts, lastDTS);
        dtsnorm = this._PTSNormalize(dts, lastDTS);
        mp4Sample.duration = (dtsnorm - lastDTS) / pes2mp4ScaleFactor;
      }
      else {
        ptsnorm = this._PTSNormalize(pts, nextAacPts);
        dtsnorm = this._PTSNormalize(dts, nextAacPts);
        let delta = Math.round(1000 * (ptsnorm - nextAacPts) / pesTimeScale);
        // if fragment are contiguous, detect hole/overlapping between fragments
        if (contiguous) {
          // log delta
          if (delta) {
            // if (delta > 0) {
            //   logger.log(`${delta} ms hole between AAC samples detected,filling it`);
            //   // if we have frame overlap, overlapping for more than half a frame duraion
            // } else if (delta < -12) {
            //   // drop overlapping audio frames... browser will deal with it
            //   logger.log(`${(-delta)} ms overlapping between AAC samples detected, drop frame`);
            //   track.len -= unit.data.byteLength; //.length; //.byteLength;
            //   continue;
            // }
            // set PTS/DTS to expected PTS/DTS
            ptsnorm = dtsnorm = nextAacPts;
          }
        }

        // remember first PTS of our aacSamples, ensure value is positive
        firstPTS = Math.max(0, ptsnorm);
        firstDTS = Math.max(0, dtsnorm);
        if (track.len > 0) {
          /* concatenate the audio data and construct the mdat in place (need 8 more bytes to fill length and mdat type) */
          mdat = new Uint8Array(track.len + 8);
          view = new DataView(mdat.buffer);
          view.setUint32(0, mdat.byteLength);
          mdat.set(MP4.types.mdat, 4);
        } else {
          // no audio samples
          return;
        }
      }

      mdat.set(unit.data, offset);
      offset += unit.data.byteLength;
      //console.log('PTS/DTS/initDTS/normPTS/normDTS/relative PTS : ${aacSample.pts}/${aacSample.dts}/${this._initDTS}/${ptsnorm}/${dtsnorm}/${(aacSample.pts/4294967296).toFixed(3)}');
      mp4Sample = {
        size: unit.data.byteLength,
        duration: aacSample.duration, 
        pts: aacSample.pts,
        dts: aacSample.dts,
        cts: 0,
        flags: { isLeading: 0, isDependedOn: 0, hasRedundancy: 0, degradPrio: 0, dependsOn: 1, }
      };
      samples.push(mp4Sample);
      lastDTS = dtsnorm;
    }

    var lastSampleDuration = 0; // 0 => 1024
    var nbSamples = samples.length;
    //set last sample duration as being identical to previous sample
    if (nbSamples >= 2) {
      lastSampleDuration = samples[nbSamples - 2].duration;
      mp4Sample.duration = lastSampleDuration;
    }

    if (nbSamples) {
      // next aac sample PTS should be equal to last sample PTS + duration
      this.nextAacPts = ptsnorm + pes2mp4ScaleFactor * lastSampleDuration;
      //logger.log('Audio/PTS/PTSend:' + aacSample.pts.toFixed(0) + '/' + this.nextAacDts.toFixed(0));
      track.len = 0;
      track.samples = samples;
      track.duration = totalDuration;
      moof = MP4.moof(track.sequenceNumber++, firstAAcSampleDts / pes2mp4ScaleFactor, track);
      track.samples = [];

      let audioData = {
        id: this.id,
        level: this.level,
        sn: this.sn_soun,
        data1: moof,
        data2: mdat,
        // startPTS: firstPTS / pesTimeScale,
        // endPTS: this.nextAacPts / pesTimeScale,
        // startDTS: firstDTS / pesTimeScale,
        // endDTS: (dtsnorm + pes2mp4ScaleFactor * lastSampleDuration) / pesTimeScale,
        type: 'audio',
        nb: nbSamples
      };
      this.observer.trigger(Event.FRAG_PARSING_DATA, audioData);
      return audioData;
    }
    return null;
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  _PTSNormalize(value, reference) {
    if (reference === undefined) {
      return value;
    }

    var offset;
    if (reference < value) {
      // - 2^33
      offset = -8589934592;
    } else {
      // + 2^33
      offset = 8589934592;
    }

    /* PTS is 33bit (from 0 to 2^33 -1)
      if diff between value and reference is bigger than half of the amplitude (2^32) then it means that
      PTS looping occured. fill the gap */
    while (Math.abs(value - reference) > 4294967296) {
      value += offset;
    }
    return value;
  }
}

export default MP4Remuxer;
