/** Buffer Controller */

import Event from '../events';
import EventHandler from '../event-handler';
import { ErrorTypes, ErrorDetails } from '../errors';

class BufferController extends EventHandler {

  constructor(jvp) {
    super(jvp,
      Event.MEDIA_ATTACHING,
      Event.BUFFER_APPENDING,
      Event.BUFFER_RESET
    );

    this.media = null;
    this.mediaSource = null;
    this.sourceBuffer = {};
    this.segments = [];
    this.appended = 0;
    this._msDuration = null;

    // Source Buffer listeners
    this.onsbue = this.onSBUpdateEnd.bind(this);
    this.onsbe = this.onSBUpdateError.bind(this);
    this.pendingTracks = {};  // not used.
    this.tracks = {};

    this.mediaType = 'H264Raw';
    this.channelName = undefined;
    this.websocketProtocol = undefined;
    this.websocketIp = undefined;
    this.websocketPort = undefined;
    this.websocketName = undefined;
    this.wsParams = undefined;
  }

  destroy() {
    EventHandler.prototype.destroy.call(this);
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  onMediaAttaching(data) {
    let media = this.media = data.media;
    this.mediaType = data.mediaType;
    this.channelName = data.channelName;
    this.websocketProtocol = data.websocketProtocol;
    this.websocketIp = data.websocketIP;
    this.websocketPort = data.websocketPort;
    this.websocketName = data.websocketName;
    this.wsParams = data.wsParams;
    if (media) {
      // setup the media source
      var ms = this.mediaSource = new MediaSource();

      //Media Source listeners
      this.onmso = this.onMediaSourceOpen.bind(this);
      this.onmse = this.onMediaSourceEnded.bind(this);
      this.onmsc = this.onMediaSourceClose.bind(this);
      ms.addEventListener('sourceopen', this.onmso);
      ms.addEventListener('sourceended', this.onmse);
      ms.addEventListener('sourceclose', this.onmsc);

      // link video and media Source
      media.src = URL.createObjectURL(ms);
    }
  }

  // { type: trackName, data: initSegment, parent: 'main' }
  onBufferAppending(data) {
    if (!this.segments) {
      this.segments = [data];
    } else {
      this.segments.push(data);
    }
    this.doAppending();
  }

  onBufferReset(tracks) {
    let sourceBuffer = this.sourceBuffer;
    for (let type in sourceBuffer) {
      let sb = sourceBuffer[type];
      try {
        this.mediaSource.removeSourceBuffer(sb);
        sb.removeEventListener('updateend', this.onsbue);
        sb.removeEventListener('error', this.onsbe);
      } catch (err) {
      }
    }
    this.sourceBuffer = {};
    this.flushRange = [];
    this.segments = [];
    this.appended = 0;

    if (this.mediaType === 'H264Raw') {
      this.createSourceBuffers(tracks); //{ tracks: 'video', mimeType: data.mimeType, avFlagIS: data.avFlagIS });
    }
    if (this.mediaType === 'JTT1078') {
      this.createSourceBuffers(tracks); //{ tracks: 'video', mimeType: data.mimeType, avFlagIS: data.avFlagIS });
    }
    this.jvp.trigger(Event.UI_EVENT, { eventName: 'BufferReset' });
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  onMediaSourceOpen() {
    let mediaSource = this.mediaSource;
    if (mediaSource) {
      // once received, don't listen anymore to sourceopen event
      mediaSource.removeEventListener('sourceopen', this.onmso);
    }

    if (this.mediaType === 'FMp4') {
      this.checkPendingTracks();
    }

    this.jvp.trigger(Event.MEDIA_ATTACHED, {
      media: this.media,
      channelName: this.channelName,
      mediaType: this.mediaType,
      websocketProtocol: this.websocketProtocol,
      websocketIp: this.websocketIp,
      websocketPort: this.websocketPort,
      websocketName: this.websocketName,
      wsParams: this.wsParams
    });
  }

  onMediaSourceEnded() {
    console.log('media source ended');
  }

  onMediaSourceClose() {
    console.log('media source closed');
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  onSBUpdateEnd(event) {
    if (this.jvp.browserType === 1) {  // Firefox
      this.mediaSource.endOfStream();
      this.media.play();
    }
    this.appending = false;
    this.doAppending();
    this.jvp.trigger(Event.UI_PLAYING, {});
  }

  onSBUpdateError(event) {
    console.log('sourceBuffer error:', event);
    // according to http://www.w3.org/TR/media-source/#sourcebuffer-append-error
    // this error might not always be fatal (it is fatal if decode error is set, in that case
    // it will be followed by a mediaElement error ...)
    this.jvp.trigger(Event.ERROR, { type: ErrorTypes.MEDIA_ERROR, details: ErrorDetails.BUFFER_APPENDING_ERROR, fatal: false });
    // we don't need to do more than that, as accordin to the spec, updateend will be fired just after
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////

  checkPendingTracks() {
    this.createSourceBuffers({ tracks: 'video', mimeType: '' });
    this.pendingTracks = {}; // not used.
  }

  // { tracks: 'video', mimeType: data.mimeType, buffer: mediaSource.addSourceBuffer(mimeType)}
  createSourceBuffers(tracks) {
    var sourceBuffer = this.sourceBuffer, mediaSource = this.mediaSource;

    var success = true;
    for (let trackName in tracks) {
      if (!sourceBuffer[trackName]) {

        let track = tracks[trackName];
        let codec = track.levelCodec || track.codec;  // use levelCodec as first priority
        let mimeType = `${track.container};codecs=${codec}`;
        console.log(`creating sourceBuffer(${mimeType})`);

        try {
          let sb = sourceBuffer[trackName] = mediaSource.addSourceBuffer(mimeType);
          sb.addEventListener('updateend', this.onsbue);
          sb.addEventListener('error', this.onsbe);
          this.tracks[trackName] = { codec: codec, container: track.container };
          track.buffer = sb;
        }
        catch (err) {
          success = false;
          console.log(`error while trying to add sourceBuffer:${err.message}`);
          this.jvp.trigger(Event.ERROR, { type: ErrorTypes.MEDIA_ERROR, details: ErrorDetails.BUFFER_ADD_CODEC_ERROR, fatal: false, err: err, mimeType: mimeType });
        }
      }
    }

    if (success) {
      console.log("createSourceBuffers OK");
      this.jvp.trigger(Event.BUFFER_CREATED, { tracks: tracks });
      this.media.loop = false;
      this.media.play();
    }
  }

  // { type: trackName, data: initSegment, parent: 'main' }
  // segments = [{type, data}], type='video'
  doAppending() {
    var jvp = this.jvp, sourceBuffer = this.sourceBuffer, segments = this.segments;
    if (Object.keys(sourceBuffer).length) {

      if (this.media.error) {
        this.segments = [];
        console.log('trying to append although a media error occured, flush segment and abort');
        return;
      }
      if (this.appending) {
        return;
      }

      if (segments && segments.length) {
        var segment = segments.shift();
        //this.jvp.priintBinaryArray(segment.data, this.websocketName);

        try {
          if (sourceBuffer[segment.type]) {
            this.parent = segment.parent;
            sourceBuffer[segment.type].appendBuffer(segment.data);
            this.appendError = 0;
            this.appended++;
            this.appending = true;
          } else {
            // do nothing
          }
        } catch (err) {
          // in case any error occured while appending, put back segment in segments table 
          segments.unshift(segment);
          var event = { type: ErrorTypes.MEDIA_ERROR };
          if (err.code !== 22) {
            if (this.appendError) {
              this.appendError++;
            } else {
              this.appendError = 1;
            }

            event.details = ErrorDetails.BUFFER_APPEND_ERROR;
            event.frag = this.fragCurrent;
            if (this.appendError > jvp.config.appendErrorMaxRetry) {
              segments = [];
              event.fatal = true;
              return;
            } else {
              event.fatal = false;
            }
          } else {
            this.segments = [];
            event.details = ErrorDetails.BUFFER_FULL_ERROR;
            return;
          }
        }
      } // end of if (segments && segments.length)
    }
  }
}

export default BufferController;
